export default {
    computed: {
        posts() {
            return this.$store.state.posts;
        },
        post() {
            return this.posts.find(el => el.slug === this.slug);
        }
    },
    data() {
        return {
            slug: this.$route.params.slug
        };
    },
    created() {
        this.$store.dispatch("getPosts");
    }
};
